.wp-block-blocks-gamestore-block-faq {
  margin-top: clamp(3.75rem, calc(2.77rem + 4.92vw), 7.5rem)!important;
  margin-bottom: clamp(3.75rem, calc(2.77rem + 4.92vw), 7.5rem)!important;
  
  &.no-margin {
    margin-top: 0!important;
    margin-bottom: 0!important;
  }

  .faq-title {
    margin-top: 0;
    margin-bottom: 24px;
    margin-left: auto;
    margin-right: auto;
  }

  .faq-inner {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }

  .faq-item {
    border-radius: 5px;
    border: 1px solid var(--divider-main, rgba(14, 13, 15, 0.08));
    padding: 24px;
    display: flex;
    gap: 24px;
    flex-direction: column;

    .faq-item-title {
      color: var(--text-primary, #0E0D0F);
      font-size: 18px;
      font-weight: 500;
      line-height: 1.2;
      display:flex;
      align-items: center;
      justify-content: space-between;
      cursor: pointer;

      &::after {
        content: "";
        width: 30px;
        height: 31px;
        display: block;
        background: url('data:image/svg+xml,<svg width="30" height="31" viewBox="0 0 30 31" fill="none" xmlns="http://www.w3.org/2000/svg"><g clip-path="url(%23clip0_1556_3182)"><path d="M13.75 30.3179V18.5679H2V17.0679H13.75V5.31787H15.25V17.0679H27V18.5679H15.25V30.3179H13.75Z" fill="url(%23paint0_linear_1556_3182)"/></g><defs><linearGradient id="paint0_linear_1556_3182" x1="-1.20377e-07" y1="15.1095" x2="30" y2="15.1095" gradientUnits="userSpaceOnUse"><stop stop-color="%233DAFE2"/><stop offset="1" stop-color="%236F76C0"/></linearGradient><clipPath id="clip0_1556_3182"><rect width="30" height="30" fill="white" transform="matrix(1 0 0 -1 0 30.3179)"/></clipPath></defs></svg>') center center no-repeat;
      }

      &.open {
        &::after {
          background: url('data:image/svg+xml,<svg width="30" height="31" viewBox="0 0 30 31" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M3 17.0679H19V18.5679H3V17.0679Z" fill="url(%23paint0_linear_1556_3176)"/><defs><linearGradient id="paint0_linear_1556_3176" x1="-1.20377e-07" y1="15.1095" x2="30" y2="15.1095" gradientUnits="userSpaceOnUse"><stop stop-color="%233DAFE2"/><stop offset="1" stop-color="%236F76C0"/></linearGradient></defs></svg>') center center no-repeat;
        }
      }
    }
    .faq-item-description {
      color: var(--text-secondary, rgba(14, 13, 15, 0.70));
      line-height: 1.5; 
      display: none;

      &.show {
        display: block;
      }

      a {
        color: var(--purple-main, #6F76C0);
        text-decoration: none;
      }
    }
  }
}